#cb{
    width: 200px;
    height: 100px;
    border-radius: 30px 30px;
    font-size: 20px;
    padding: 10px;
    border: 2px solid black; 
    animation: new 1s forwards;
}

@keyframes new{
    from{    
        border: 2px solid black;
    }
    to{
        border: 2px solid white;
    }
}

.CoinBlockRISE{
    background: linear-gradient(to bottom right, #ffd1d1 0%, #ff6161 100%);
    /* background: linear-gradient(0deg, rgba(255,179,179,0.41780462184873945) 0%, rgba(255,160,160,1) 47%, rgba(255,107,107,1) 100%); */
}
.CoinBlockEVEN{
    background: linear-gradient(to bottom right, #b5b5b5 0%, #6b6b6b 100%);
}
.CoinBlockFALL{
    background-color: #045de9;
    background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
    /* background: linear-gradient(0deg, rgba(179,183,255,0.41780462184873945) 0%, rgba(166,160,255,1) 47%, rgba(115,107,255,1) 100%); */
}

.coinImg{
    height: 20px;
    width: 20px;
}
