.Screen{
    position: absolute;
    background-color: skyblue;
    animation: fadeout 1s 0.5s forwards;
    width: 100%;
    height: 100vh;
    z-index: 1;
    opacity: 1;
}

@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        width: 0;
    }
}

.CoinTrade{
    width: 100%;
}

.contents{
    position: absolute;
    height: calc(95% - 62x);
    left: calc(140px);
    width: calc(95% - 140px);
}