.nav{
    padding-top: 100px;
    position: fixed;
    width: 100px;
    height: 100%;
    box-shadow: 0 0 20px 2px white;
    
    animation: reverseshadow 0.5s forwards;
}

.nav:hover{
    animation: shadow 0.5s forwards;
}

.link{
    cursor: pointer;
    display: grid;
    margin: auto;
    border-radius: 15px;
    width: 66px;
    height: 66px;
    transition: color 0.4 cubic-bezier(0, 0, 0.2, 1);
    margin-bottom: 10px;
    text-decoration: none;
    animation: reverseshadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
}

.link:hover{
    animation: shadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
}
i{
    font-size: 30px;
}
i,p{
    margin: auto;
    text-align: center;
    color: black;
}

@keyframes shadow{
    from{
        box-shadow: 0 0 10px 2px white;
    }
    to{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

@keyframes reverseshadow{
    from{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
    to{
        box-shadow: 0 0 10px 2px white;
    }
}