@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
html, body{
 	width: 100%;
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Nanum Gothic', sans-serif;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

.nav{
    padding-top: 100px;
    position: fixed;
    width: 100px;
    height: 100%;
    box-shadow: 0 0 20px 2px white;
    
    -webkit-animation: reverseshadow 0.5s forwards;
    
            animation: reverseshadow 0.5s forwards;
}

.nav:hover{
    -webkit-animation: shadow 0.5s forwards;
            animation: shadow 0.5s forwards;
}

.link{
    cursor: pointer;
    display: grid;
    margin: auto;
    border-radius: 15px;
    width: 66px;
    height: 66px;
    transition: color 0.4 cubic-bezier(0, 0, 0.2, 1);
    margin-bottom: 10px;
    text-decoration: none;
    -webkit-animation: reverseshadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
            animation: reverseshadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
}

.link:hover{
    -webkit-animation: shadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
            animation: shadow 0.5s forwards cubic-bezier(0, 0, 0.2, 1);
}
i{
    font-size: 30px;
}
i,p{
    margin: auto;
    text-align: center;
    color: black;
}

@-webkit-keyframes shadow{
    from{
        box-shadow: 0 0 10px 2px white;
    }
    to{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

@keyframes shadow{
    from{
        box-shadow: 0 0 10px 2px white;
    }
    to{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
}

@-webkit-keyframes reverseshadow{
    from{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
    to{
        box-shadow: 0 0 10px 2px white;
    }
}

@keyframes reverseshadow{
    from{
        box-shadow: 0 0 10px 2px #7e7e7e;
    }
    to{
        box-shadow: 0 0 10px 2px white;
    }
}
.Screen{
    position: absolute;
    background-color: skyblue;
    -webkit-animation: fadeout 1s 0.5s forwards;
            animation: fadeout 1s 0.5s forwards;
    width: 100%;
    height: 100vh;
    z-index: 1;
    opacity: 1;
}

@-webkit-keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        width: 0;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        width: 0;
    }
}

.CoinTrade{
    width: 100%;
}

.contents{
    position: absolute;
    height: calc(95% - 62x);
    left: calc(140px);
    width: calc(95% - 140px);
}
#cb{
    width: 200px;
    height: 100px;
    border-radius: 30px 30px;
    font-size: 20px;
    padding: 10px;
    border: 2px solid black; 
    -webkit-animation: new 1s forwards; 
            animation: new 1s forwards;
}

@-webkit-keyframes new{
    from{    
        border: 2px solid black;
    }
    to{
        border: 2px solid white;
    }
}

@keyframes new{
    from{    
        border: 2px solid black;
    }
    to{
        border: 2px solid white;
    }
}

.CoinBlockRISE{
    background: linear-gradient(to bottom right, #ffd1d1 0%, #ff6161 100%);
    /* background: linear-gradient(0deg, rgba(255,179,179,0.41780462184873945) 0%, rgba(255,160,160,1) 47%, rgba(255,107,107,1) 100%); */
}
.CoinBlockEVEN{
    background: linear-gradient(to bottom right, #b5b5b5 0%, #6b6b6b 100%);
}
.CoinBlockFALL{
    background-color: #045de9;
    background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
    /* background: linear-gradient(0deg, rgba(179,183,255,0.41780462184873945) 0%, rgba(166,160,255,1) 47%, rgba(115,107,255,1) 100%); */
}

.coinImg{
    height: 20px;
    width: 20px;
}

.Home{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(3,auto);
}

.Home .Main{
    grid-column: 1 / 4;
}

#cd{
    width: calc(100% - 450px);
    height: 200px;
    border-radius: 30px 30px;
    font-size: 20px;
    padding: 10px;
}
.CoinDetailRISE{
    background: linear-gradient(to bottom right, #ffd1d1 0%, #ff6161 100%);
    /* background: linear-gradient(0deg, rgba(255,179,179,0.41780462184873945) 0%, rgba(255,160,160,1) 47%, rgba(255,107,107,1) 100%); */
}
.CoinDetailEVEN{
    background: linear-gradient(to bottom right, #b5b5b5 0%, #6b6b6b 100%);
}
.CoinDetailFALL{
    background-color: #045de9;
    background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
    /* background: linear-gradient(0deg, rgba(179,183,255,0.41780462184873945) 0%, rgba(166,160,255,1) 47%, rgba(115,107,255,1) 100%); */
}


.CoinDetail .content{
    display: flex;
}

.CoinDetail .content .mainContent{
    width: 400px;
    margin-right: 300px;
    display: grid;
    grid-template-columns:repeat(4,1fr);
    grid-template-rows: repeat(4,1fr);
}

.CoinDetail .coinImg{
    grid-row: 1/3;
    height: 20px;
    width: 20px;
}

.CoinDetail .koreanName{
    grid-column: 2/4;
    grid-row: 1/3;
}

.CoinDetail .tradePrice{
    grid-column: 1/5;
    text-align: center;
}

.CoinDetail .changePrice{
    grid-column: 1/3;
    text-align: left;
}

.CoinDetail .changePercent{
    grid-column: 3/5;
    text-align: right;
}

.CoinDetail .content .detailContent{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: repeat(4,1fr);
}

.OrderBook{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(16,1fr);
    width: calc(100% - 450px);
}

.OrderBook .OrderPriceBlockDiscription{
    text-align: center;
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
}

.OrderBook .OrderPriceBlock{
    text-align: center;
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
}
